// 

"use client";

import { useParams } from "next/navigation";
import { useQuery } from "@tanstack/react-query";
import { fetchNoteById } from "@/lib/api";
import { Note } from "@/types/note";
import css from "./NoteDetails.module.css";

export default function NoteDetailsClient() {
  const { id } = useParams<{ id: string }>(); // ✅ берём id из URL

  const { data: note, isLoading, error } = useQuery<Note>({
    queryKey: ["note", id],
    queryFn: () => fetchNoteById(id),
    enabled: !!id, // не запускаем запрос, пока нет id
  });

  if (isLoading) return <p>Loading...</p>;
  if (error) return <p>Failed to load note.</p>;
  if (!note) return <p>Note not found.</p>;

  const formattedDate = new Date(note.createdAt).toLocaleDateString("en-GB");

  return (
    <div className={css.container}>
      <h2 className={css.title}>{note.title}</h2>
      <p className={css.content}>{note.content}</p>
      <p className={css.date}>Created: {formattedDate}</p>
    </div>
  );
}